// src/index.js
import React from 'react';
import { createRoot } from 'react-dom/client';
import './index.css';
import App from './App';
import { AppProvider } from './AppContext';

const container = document.getElementById('root');
const root = createRoot(container);

root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);


// ---- HARD RESET OF SERVICE WORKERS & CACHES ----
if (typeof window !== 'undefined' && 'serviceWorker' in navigator) {
  // Unregister all service workers for this origin
  navigator.serviceWorker.getRegistrations().then((registrations) => {
    registrations.forEach((registration) => {
      registration.unregister().catch((err) => {
        console.error('SW unregister error:', err);
      });
    });
  });

  // Clear all Cache Storage entries for this origin
  if ('caches' in window) {
    caches.keys().then((keys) => {
      keys.forEach((key) => {
        caches
          .delete(key)
          .catch((err) => console.error('Cache delete error:', err));
      });
    });
  }
}
