// src/Dashboard.js
import React, { useEffect, useState } from 'react';
import './Dashboard.css';
import { useHistory } from 'react-router-dom';
import { useApp } from './AppContext';
import {
  Button,
  Card,
  CardContent,
  CardActions,
  Chip,
  CircularProgress,
  Tabs,
  Tab,
  Typography,
} from '@mui/material';

function Dashboard() {
  const history = useHistory();
  const {
    currentUser,
    isAuthenticated,
    loading,
    getUserBookings,
    fetchBookings,
    updateBookingStatus,
    isAdmin,
  } = useApp();

  const [tab, setTab] = useState(0);
  const [loadingBookings, setLoadingBookings] = useState(false);

  //  Redirect ONLY after auth hydration is finished
  useEffect(() => {
    if (loading) return; // still hydrating session
    if (!isAuthenticated) {
      history.replace('/login');
    }
  }, [loading, isAuthenticated, history]);

  //  Fetch bookings ONLY after:
  // - hydration finished
  // - authenticated
  // - currentUser is present (prevents refresh empty/loop issues)
  useEffect(() => {
    const load = async () => {
      if (loading) return;
      if (!isAuthenticated) return;
      if (!currentUser?.id) return;

      setLoadingBookings(true);
      try {
        await fetchBookings();
      } catch (e) {
        console.error('Dashboard: fetchBookings error:', e);
      } finally {
        setLoadingBookings(false);
      }
    };

    load();
  }, [loading, isAuthenticated, currentUser?.id, fetchBookings]);

  //  While hydrating, show spinner (dont render empty state yet)
  if (loading) {
    return (
      <div className="dashboard dashboard--center">
        <CircularProgress />
      </div>
    );
  }

  //  If not authenticated after hydration, user is redirected; keep UI safe
  if (!isAuthenticated) {
    return (
      <div className="dashboard dashboard--center">
        <CircularProgress />
      </div>
    );
  }

  //  If authenticated but user object not ready yet, show loading
  // (this avoids "empty dashboard" flash on refresh)
  if (!currentUser) {
    return (
      <div className="dashboard dashboard--center">
        <CircularProgress />
      </div>
    );
  }

  const allBookings = getUserBookings();

  const bookingsByStatus = {
    all: allBookings,
    pending: allBookings.filter((b) => b.status === 'pending'),
    approved: allBookings.filter((b) => b.status === 'approved'),
    rejected: allBookings.filter((b) => b.status === 'rejected'),
    cancelled: allBookings.filter((b) => b.status === 'cancelled'),
  };

  const tabKeys = ['all', 'pending', 'approved', 'rejected', 'cancelled'];
  const currentKey = tabKeys[tab] || 'all';
  const bookingsToShow = bookingsByStatus[currentKey];

  const isBoarder = currentUser.user_type === 'student';
  const isLandlord = currentUser.user_type === 'landlord';

  const handleStatusChange = async (bookingId, status) => {
    try {
      await updateBookingStatus(bookingId, status);
    } catch (error) {
      console.error('updateBookingStatus error:', error);
      alert(error?.message || 'Failed to update booking. Please try again.');
    }
  };

  const statusChipColor = (status) => {
    switch (status) {
      case 'approved':
        return 'success';
      case 'pending':
        return 'warning';
      case 'rejected':
      case 'cancelled':
        return 'default';
      default:
        return 'default';
    }
  };

  const statusLabel = (status) => {
    switch (status) {
      case 'approved':
        return 'Approved';
      case 'pending':
        return 'Pending';
      case 'rejected':
        return 'Rejected';
      case 'cancelled':
        return 'Cancelled';
      default:
        return status;
    }
  };

  return (
    <div className="dashboard">
      <div className="dashboard__header">
        <div>
          <Typography variant="h5">
            {isBoarder ? 'My Bookings' : 'Booking Requests'}
          </Typography>
          <Typography variant="body2" color="textSecondary">
            Chat to confirm visitation dates, move-in dates, and any other details.
          </Typography>
        </div>

        <div className="dashboard__headerActions">
          {!isLandlord && (
            <Button
              variant="outlined"
              size="small"
              onClick={() => history.push('/apply-landlord')}
              style={{ marginRight: 8 }}
            >
              Apply as landlord
            </Button>
          )}

          {isAdmin && (
            <Button
              variant="contained"
              size="small"
              onClick={() => history.push('/admin/landlords')}
            >
              Review landlord applications
            </Button>
          )}
        </div>
      </div>

      <div className="dashboard__tabs">
        <Tabs
          value={tab}
          onChange={(_, newValue) => setTab(newValue)}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
        >
          <Tab label={`All (${bookingsByStatus.all.length})`} />
          <Tab label={`Pending (${bookingsByStatus.pending.length})`} />
          <Tab label={`Approved (${bookingsByStatus.approved.length})`} />
          <Tab label={`Rejected (${bookingsByStatus.rejected.length})`} />
          <Tab label={`Cancelled (${bookingsByStatus.cancelled.length})`} />
        </Tabs>
      </div>

      {loadingBookings ? (
        <div className="dashboard__loading">
          <CircularProgress />
        </div>
      ) : bookingsToShow.length === 0 ? (
        <div className="dashboard__empty">
          <Typography variant="body2" color="textSecondary">
            No bookings in this category yet.
          </Typography>
        </div>
      ) : (
        <div className="dashboard__list">
          {bookingsToShow.map((b) => {
            const otherName = isBoarder ? b.landlord_name : b.student_name;
            const otherRole = isBoarder ? 'Landlord' : 'Boarder';

            return (
              <Card key={b.id} className="dashboard__card">
                <CardContent>
                  <div className="dashboard__cardHeader">
                    <Typography variant="subtitle1" className="dashboard__cardTitle">
                      {b.listing_title}
                    </Typography>
                    <Chip
                      size="small"
                      label={statusLabel(b.status)}
                      color={statusChipColor(b.status)}
                    />
                  </div>

                  <Typography variant="body2" color="textSecondary">
                    {otherRole}: {otherName}
                  </Typography>

                  <Typography variant="body2" color="textSecondary">
                    Move-in date: {b.move_in_date || 'Not set'}
                  </Typography>

                  <Typography variant="body2" color="textSecondary">
                    Visitation date: {b.visitation_date || 'Not set'}
                  </Typography>

                  {b.notes && (
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      style={{ marginTop: 4 }}
                    >
                      Your message: {b.notes}
                    </Typography>
                  )}
                </CardContent>

                <CardActions className="dashboard__cardActions">
                  <Button
                    size="small"
                    variant="outlined"
                    onClick={() => history.push(`/bookings/${b.id}/chat`)}
                  >
                    Open chat
                  </Button>

                  {isLandlord && b.status === 'pending' && (
                    <>
                      <Button
                        size="small"
                        color="primary"
                        onClick={() => handleStatusChange(b.id, 'approved')}
                      >
                        Approve
                      </Button>
                      <Button
                        size="small"
                        color="secondary"
                        onClick={() => handleStatusChange(b.id, 'rejected')}
                      >
                        Reject
                      </Button>
                    </>
                  )}
                </CardActions>
              </Card>
            );
          })}
        </div>
      )}
    </div>
  );
}

export default Dashboard;
