// src/AdminLandlords.js
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useApp } from './AppContext';
import {
  Button,
  CircularProgress,
  Typography,
  Card,
  CardContent,
  CardActions,
} from '@mui/material';

function AdminLandlords() {
  const {
    isAdmin,
    isAuthenticated,
    fetchLandlordApplications,
    approveLandlordApplication,
    rejectLandlordApplication,
  } = useApp();
  const history = useHistory();

  const [applications, setApplications] = useState([]);
  const [loading, setLoading] = useState(true);

  // If not logged in at all, send to login
  useEffect(() => {
    if (!isAuthenticated && !loading) {
      history.replace('/login');
    }
  }, [isAuthenticated, loading, history]);

  // Load applications when user is admin
  useEffect(() => {
    if (!isAuthenticated) {
      setLoading(false);
      return;
    }

    if (!isAdmin) {
      // Logged in but not admin
      setLoading(false);
      return;
    }

    const loadApplications = async () => {
      setLoading(true);
      const apps = await fetchLandlordApplications();
      setApplications(apps);
      setLoading(false);
    };

    loadApplications();
  }, [isAuthenticated, isAdmin, fetchLandlordApplications]);

  if (!isAuthenticated) {
    return (
      <div className="dashboard dashboard--center">
        <Typography>Please log in to view this page.</Typography>
      </div>
    );
  }

  if (!isAdmin) {
    return (
      <div className="dashboard dashboard--center">
        <Typography>You do not have admin access.</Typography>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="dashboard dashboard--center">
        <CircularProgress />
      </div>
    );
  }

  return (
    <div className="dashboard">
      <div className="dashboard__header">
        <div>
          <Typography variant="h5">Landlord Applications</Typography>
          <Typography variant="body2" color="textSecondary">
            Review landlord sign-up requests, then approve or reject.
          </Typography>
        </div>
      </div>

      {applications.length === 0 ? (
        <div className="dashboard__empty">
          <Typography variant="body2" color="textSecondary">
            No landlord applications pending approval.
          </Typography>
        </div>
      ) : (
        <div className="dashboard__list">
          {applications.map((app) => (
            <Card key={app.id} className="dashboard__card">
              <CardContent>
                <Typography variant="subtitle1" gutterBottom>
                  {app.full_name}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  Email: {app.email}
                </Typography>
                {app.business_name && (
                  <Typography variant="body2" color="textSecondary">
                    Business: {app.business_name}
                  </Typography>
                )}
                <Typography variant="body2" color="textSecondary">
                  Submitted: {new Date(app.created_at).toLocaleString()}
                </Typography>
              </CardContent>
              <CardActions className="dashboard__cardActions">
                <Button
                  size="small"
                  variant="contained"
                  color="primary"
                  onClick={() => approveLandlordApplication(app.id)}
                >
                  Approve
                </Button>
                <Button
                  size="small"
                  variant="outlined"
                  color="secondary"
                  onClick={() => rejectLandlordApplication(app.id)}
                  style={{ marginLeft: 8 }}
                >
                  Reject
                </Button>
              </CardActions>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}

export default AdminLandlords;
